/**
 * @file Browser client connection management class
 * @author Shinichi Tomita <shinichi.tomita@gmail.com>
 */

'use strict';

var events = require('events'),
    inherits = require('inherits'),
    qs = require('querystring'),
    isFunction = require('lodash/isFunction'),
    extend = require('lodash/extend'),
    Connection = require('../connection'),
    OAuth2 = require('../oauth2');

/**
 * @private
 */
function popupWin(url, w, h) {
  var left = (screen.width/2)-(w/2);
  var top = (screen.height/2)-(h/2);
  return window.open(url, '_blank', 'location=yes,toolbar=no,status=no,menubar=no,width='+w+',height='+h+',top='+top+',left='+left);
}

function detectIE() {
    var ua = window.navigator.userAgent;

    var msie = ua.indexOf('MSIE ');
    if (msie > 0) {
        // IE 10 or older => return version number
        return parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
    }

    var trident = ua.indexOf('Trident/');
    if (trident > 0) {
        // IE 11 => return version number
        var rv = ua.indexOf('rv:');
        return parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
    }

    var edge = ua.indexOf('Edge/');
    if (edge > 0) {
       // Edge (IE 12+) => return version number
       return parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
    }

    // other browser
    return false;
}

function handleCallbackResponse(popup) {
  var res = checkCallbackResponse(popup);
  var state = localStorage.getItem('jsforce_state');

  if (res && state && res.body.state === state) {
    localStorage.removeItem('jsforce_state');
    var states = state.split('.');
    var prefix = states[0], promptType = states[1];
    var cli = new Client(prefix);
    if (res.success) {
      cli._storeTokens(res.body);
      location.hash = '';
    } else {
      cli._storeError(res.body);
    }
    if (promptType === 'popup' && popup.close) {
      popup.close();
    }
    return true;
  }

  return false;
}

/**
 * @private
 */
function checkCallbackResponse(popup) {
  var params;

  if (popup.location.hash) {
    params = qs.parse(popup.location.hash.substring(1));
    if (params.access_token) {
      return { success: true, body: params };
    }
  } else if (popup.location.search) {
    params = qs.parse(popup.location.search.substring(1));
    if (params.error) {
      return { success: false, body: params };
    }
  }
}

/** @private **/
var clientIdx = 0;


/**
 * @class
 * @todo add document
 */
var Client = function(prefix) {
  this._prefix = prefix || 'jsforce' + clientIdx++;
  this.connection = null;
};

inherits(Client, events.EventEmitter);

function refreshTokenDelegate(conn, callback) {
  var oauth2 = new OAuth2(this.config);

  var refreshToken = localStorage.getItem(this._prefix + '_refresh_token');

  if(!refreshToken) {
    throw new Error('no refresh token');
  }

  var clientInstance  = this;

  return oauth2.refreshToken(refreshToken)
    .then(function(result) {
      localStorage.setItem(clientInstance._prefix + '_access_token', result.access_token);
      return callback(null, result.access_token, result);
    })
    .catch(callback);
}

/**
 *
 */
Client.prototype.init = function(config) {
  this.config = config;
  config.refreshFn = refreshTokenDelegate.bind(this);
  this.connection = new Connection(config);
  var tokens = this._getTokens();
  if (tokens) {
    this.connection.initialize(tokens);
    var self = this;
    setTimeout(function() {
      self.emit('connect', self.connection);
    }, 10);
  }
};

/**
 *
 */
Client.prototype.login = function(options, callback) {
  if (isFunction(options)) {
    callback = options;
    options = {};
  }
  options = options || {};
  callback = callback || function(){ };
  extend(options, this.config);
  var self = this;
  this._prompt(options, callback);
};


Client.prototype._prompt = function(options, callback) {
  var self = this;
  var oauth2 = new OAuth2(options);
  var rand = Math.random().toString(36).substring(2);
  var state = [ this._prefix, "popup", rand ].join('.');
  localStorage.setItem("jsforce_state", state);
  var authzUrl = oauth2.getAuthorizationUrl({
    response_type: 'token',
    scope : options.scope,
    state: state
  });
  var size = options.size || {};

  if(!detectIE()) {
    var pw = popupWin(authzUrl, size.width || 912, size.height || 513);
  }

  if (!pw) {
    state = [ this._prefix, "redirect", rand ].join('.');
    localStorage.setItem("jsforce_state", state);
    authzUrl = oauth2.getAuthorizationUrl({
      response_type: 'token',
      scope : options.scope,
      state: state
    });
    location.href = authzUrl;
    return;
  }

  self._removeTokens();

  var cordovaLoadFinished = false;

  pw.addEventListener("loaderror", function(event) {
    if(event.url.indexOf(options.redirectUri) > -1) {
      handleCallbackResponse({
        location: new URL(event.url),
        promptType: 'cordova-in-app-browser'
      });

      pw.close();

      cordovaLoadFinished = true;
    }
  });

  pw.addEventListener("loadstop", function(event) {
    if(event.url.indexOf(options.redirectUri) > -1) {
      handleCallbackResponse({
        location: new URL(event.url),
        promptType: 'cordova-in-app-browser'
      });

      pw.close();

      cordovaLoadFinished = true;
    }
  });

  var pid = setInterval(function() {
    try {
      if (!pw || pw.closed || cordovaLoadFinished || handleCallbackResponse(pw)) {
        clearInterval(pid);
        var tokens = self._getTokens();
        if (tokens) {
          self.connection.initialize(tokens);
          self.emit('connect', self.connection);
          callback(null, { status: 'connect' });
        } else {
          var err = self._getError();
          if (err) {
            callback(new Error(err.error + ": " + err.error_description));
          } else {
            callback(null, { status: 'cancel' });
          }
        }
      }
    } catch(e) {}
  }, 1000);
};

/**
 *
 */
Client.prototype.isLoggedIn = function() {
  return !!(this.connection && this.connection.accessToken);
};

/**
 *
 */
Client.prototype.logout = function() {
  this._removeTokens();
  this.emit('disconnect');
  return this.connection.logout();
};

/**
 * @private
 */
Client.prototype._getTokens = function() {
  if (localStorage.getItem(this._prefix + '_access_token') !== null) {
    var userInfo;
    var idUrl = localStorage.getItem(this._prefix + '_id');
    if (idUrl) {
      var ids = idUrl.split('/');
      userInfo = { id: ids.pop(), organizationId: ids.pop(), url: idUrl };
    }
    return {
      accessToken: localStorage.getItem(this._prefix + '_access_token'),
      refreshToken: localStorage.getItem(this._prefix + '_refresh_token'),
      instanceUrl: this.config && this.config.instanceUrl ? this.config.instanceUrl : localStorage.getItem(this._prefix + '_instance_url'),
      userInfo: userInfo
    };
  }
  return null;
};

/**
 * @private
 */
Client.prototype._storeTokens = function(params) {
  if(params.refresh_token) {
    localStorage.setItem(this._prefix + '_refresh_token', params.refresh_token);
  }

  localStorage.setItem(this._prefix + '_access_token', params.access_token);
  localStorage.setItem(this._prefix + '_instance_url', params.instance_url);
  localStorage.setItem(this._prefix + '_issued_at', params.issued_at);
  localStorage.setItem(this._prefix + '_id', params.id);
};

/**
 * @private
 */
Client.prototype._removeTokens = function() {
  localStorage.removeItem(this._prefix + '_access_token');
  localStorage.removeItem(this._prefix + '_instance_url');
  localStorage.removeItem(this._prefix + '_issued_at');
  localStorage.removeItem(this._prefix + '_id');
  localStorage.removeItem(this._prefix + '_refresh_token');
};

/**
 * @private
 */
Client.prototype._getError = function() {
  try {
    var err = JSON.parse(localStorage.getItem(this._prefix + '_error'));
    localStorage.removeItem(this._prefix + '_error');
    return err;
  } catch(e) {}
};

/**
 * @private
 */
Client.prototype._storeError = function(err) {
  localStorage.setItem(this._prefix + '_error', JSON.stringify(err));
};


function checkHash() {
  if(handleCallbackResponse(window)) {
    window.location.hash = '';
  }
}

checkHash();

/**
 *
 */
module.exports = new Client();

module.exports.Client = Client;
